body {
  width: 1440px;
}
.header {
  display: flex;                /* 좌우 정렬 */
  justify-content: space-between; /* 양 끝에 배치 */
  align-items: center;          /* 세로 가운데 정렬 */
  padding: 0 20px;
  height: 60px;
  background: #fff;             /* 배경색 (필요 시 수정) */
  border-bottom: 1px solid #ddd; /* 구분선 */
}

.logo a {
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
}

.inner {
  display: flex;
  gap: 20px;   /* 메뉴 간격 */
}

.nav {
  text-decoration: none;
  color: #333;
  font-size: 1rem;
  padding: 5px 10px;
  transition: color 0.2s, background 0.2s;
}

.nav:hover {
  color: #fff;
  background: #9c9c9c;
  border-radius: 4px;
}

/* 현재 선택된 메뉴 강조 (NavLink의 active 상태) */
.nav.active {
  color: #fff;
  background: #555;
  border-radius: 4px;
}

.footer{
  height: 80px;
  background-color: rgb(163, 162, 162);
  line-height: 80px; text-align: center; color: black; font-size: 24px;
}